@charset "utf-8";

// Import a Google Font
@import url('https://fonts.googleapis.com/css?family=Nunito:400,700');
@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap');

// Import only what you need from Bulma

$purple: #956EDC;
$turquoise: #08CAA1;

// Update Bulma's global variables
$primary: $turquoise;
$success: $turquoise;
$warning: #ec7238;
$danger: #dd5843;

// Social Media Colours
$linkedin: #0077B5;
$facebook: #3B5998;
$whatsapp: #25D366;
$trello: #0079BF;
$telegram: #1E96C8;
$slack: #611f69;

$breadcrumb-item-separator-color: #D1D1D6;

@import "../node_modules/bulma/bulma.sass";
@import "../node_modules/bulma-extensions/bulma-steps/src/sass/index.sass";
@import "../node_modules/bulma-slider/src/sass/index.sass";
@import "../node_modules/bulma-extensions/bulma-checkradio/src/sass/index.sass";
@import "../node_modules/bulma-extensions/src/sass/index.sass";

body {
  font-family: "Source Sans Pro"; }

h1 {
  font-family: "Source Sans Pro";
  font-size: 34px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 42px; }

h2 {
  font-family: "Source Sans Pro";
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 32px; }


h3 {
  font-family: "Source Sans Pro";
  font-size: 21px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 27px;
  margin-bottom: 10px; }


h4 {
  font-family: "Source Sans Pro";
  font-size: 21px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 27px; }


h5 {
  font-family: "Source Sans Pro";
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 20px; }


h6 {
  font-family: "Source Sans Pro";
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 17px; }

.linkedin-link {
  color: #0e76a8;
  &:hover {
    color: $turquoise; }
  &:active {
    color: $turquoise; } }

.twitter-link {
  color: #00acee;
  &:hover {
    color: $turquoise; }
  &:active {
    color: $turquoise; } }

.instagram-link {
  color: #fb3958;
  &:hover {
    color: $turquoise; }
  &:active {
    color: $turquoise; } }



a {
  color: #4A4A4A;
  &:hover {
    text-decoration: underline; } }

.icon-link {
  &:hover {
    text-decoration: none; } }

.react-autosuggest__input {
  @extend .input; }

.inline-icon-button {
  cursor: pointer;
  color: #d1d1d6;
  &:hover {
    color: #9b9b9b;
    text-decoration: underline; } }

.new-messages-badge {
  cursor: pointer; }

.breadcrumb-link {
  color: $purple !important; }

.is-active .breadcrumb-link {
  color: #4a4a4a !important; }

.section {
  padding-top: 24px; }

.job-overview-side-candidates {
  border-left: 1px solid #E4E4E4;
  padding-left: 24px !important; }

.no-underline {
  text-decoration: none !important;
  &:hover {
    text-decoration: none !important; } }

.is-linkedin {
  background-color: $linkedin; }

.is-facebook {
  background-color: $facebook; }

.is-whatsapp {
  background-color: $whatsapp; }

.is-trello {
  background-color: $trello; }

.is-telegram {
  background-color: $telegram; }

.is-slack {
  background-color: $slack; }
